.reminder-wrap {
	position: relative;
	display: block;
	@include max-md {
		.container {
			padding: 0;
		}
	}
	.reminder-content {
		position: relative;
		width: 100%;
		padding: 8rem 3rem 3rem 20%;
		@include max-md {
			padding-left: 2rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		display: flex;
		align-items: center;
		margin: 0;
		margin-right: auto;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
