.rsvp-section {
	position: relative;
	padding: 3rem 0 5rem;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 3rem;
		@include max-md {
			padding: 5rem 2rem;
		}
		.elements-wrapper-absolute {
			mask-image: url(../images/frame-mask.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
