.thank-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.frame {
		width: 104%;
	}
	.title-section {
		text-align: left;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		@include max-md {
			width: 100%;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask-image: url(../images/frame-mask.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		@include max-md {
			.image-container {
				padding: 0;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			text-align: left;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
