.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6.5rem;
			align-items: center;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				width: 30%;
				min-height: 80%;
				background-color: var(--color-primary);
				color: var(--color-dark);
				mask-image: url(../images/frame-mask.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				@include max-xl {
					width: 40%;
				}
				@include max-lg {
					width: 70%;
				}
				@include max-md {
					width: 100%;
				}
				.greeting-wrapper {
					position: relative;
					margin-bottom: 1rem;
				}
				h6 {
					font-weight: 700;
					margin-bottom: 0;
				}
				.cover-description {
					position: relative;
					width: 100%;
					padding: 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 1rem;
					}
					.title {
						padding: 1rem 0;
						line-height: 1.2;
					}
					p {
						margin-bottom: 0;
					}
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
