.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0 0;
		background-color: var(--color-primary);
		color: var(--color-dark);
		@include max-md {
			padding: 3rem 0 0;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		text-align: left;
		margin-bottom: 3rem;
		.title-section {
			text-align: left;
		}
	}
	.couple {
		position: relative;
		padding: 5rem 0;
		text-align: center;
		background-color: var(--color-main);
		mask-image: url(../images/frame-mask.png);
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		margin-bottom: -5rem;
		z-index: 10;
		@include max-md {
			padding: 3rem 0;
			&.man {
				margin-bottom: 0;
			}
		}
		.frame {
			width: 103%;
			height: 102%;
		}
		.image-wrap {
			width: 60%;
			position: relative;
			aspect-ratio: 5/6;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 0;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
